import React from 'react';
import PropTypes from 'prop-types';
import NewItem from '../components/NewItem';

class ShoppingCart extends React.Component {
  constructor() {
    super();
    this.state = {
      shoppingCart: [],
    };
    this.handleDelete = this.handleDelete.bind(this);
  }

  componentDidMount() {
    this.filterFunction();
  }

  /** Fun√ß√£o respons√°vel por deletar um item do carrinho de comprar. */
  handleDelete(product) {
    /** Esta fun√ß√£o faz a seguinte verifica√ß√£o */
    /** Primeiramente, √© recebido o carrinho por props, o carrinho de compras, que √©
     * um array */
    const { shoppingCart } = this.state;

    /**
     * 1. Logo, abaixo √© criada uma const newList, que receber√° um filtro do array
     * shoppingCart.
     * 2. Ser√° feita uma verifica√ß√£o, a cada itera√ß√£o.
     * 3. Algum "item" de "shoppingCart" tem uma ID diferente a ID do produto recebido no
     * par√¢metro da fun√ß√£o?
     * 4. Se for diferente, v√° adicionando ao "newList" */
    const newList = shoppingCart.filter((item) => item.id !== product.id);

    /** Ap√≥s o t√©rmino da itera√ß√£o, o novo array √© criado e adicionado ao State. */
    this.setState({
      shoppingCart: newList,
    });
  }

  /** Fun√ß√£o abaixo √© chamada toda vez que o componente √© montado,
   * ou seja, quando se clica em algo que direcione ao /carrinho */
  filterFunction() {
    /** A props abaixo vem do componente App.js */
    const { cart } = this.props;

    /** Basicamente, a fun√ß√£o abaixo tem a tarefa de verificar
     * se um determinado item j√° se encontra no carrinho.
     */
    const result = cart.reduce((acc, curr) => (
      acc.includes(curr) ? acc : acc.concat(curr)
    ), []);
    this.setState({
      shoppingCart: result,
    });
  }

  render() {
    const { cart, increaseQuantity, decreaseQuantity } = this.props;
    const { shoppingCart } = this.state;

    const emptyCart = (
      <h3 data-testid="shopping-cart-empty-message">
        Seu carrinho est√° vazio
      </h3>);

    /** Esta condicional abaixo verifica se o carrinho est√° vazio.
       * Caso esteja, renderiza uma mensagem "Seu carriho est√° vazio"
       * Caso contenha algum item, √© feita uma renderiza√ß√£o din√¢mica
       * do componente NewItem. De acordo com a quantidade itens no
       * carrinho, ser√° a quantidade de itera√ß√µes que o .map ir√° fazer
       * abaixo */
    return !cart.length ? emptyCart : shoppingCart.map((product, index) => (
      <NewItem
      /** Para NewItem, s√£o passadas algunas props como o "product" que
       * est√° sendo trabalhado na itera√ß√£o recorrente, passado "handleDelete",
       * que tem a fun√ß√£o de teletar um item do carrinho, "increaseQuantity" e
       * "decreaseQuantity", que tem fun√ß√£o de incrementar e decrementar, respe-
       * ctivamente */
        product={ product }
        key={ index }
        handleDelete={ this.handleDelete }
        increaseQuantity={ increaseQuantity }
        decreaseQuantity={ decreaseQuantity }
      />
    ));
  }
}

ShoppingCart.propTypes = {
  cart: PropTypes.shape({
    map: PropTypes.func.isRequired,
    length: PropTypes.arrayOf().isRequired,
    reduce: PropTypes.func.isRequired,
  }).isRequired,
  increaseQuantity: PropTypes.func.isRequired,
  decreaseQuantity: PropTypes.func.isRequired,
};

export default ShoppingCart;
