import React from 'react';
import './App.css';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Home from './pages/Home';
import ShoppingCart from './pages/ShoppingCart';
import Details from './pages/Details';

class App extends React.Component {
  constructor() {
    super();
    this.state = { cart: [] };

    this.addState = this.addState.bind(this);
    this.increaseQuantity = this.increaseQuantity.bind(this);
    this.decreaseQuantity = this.decreaseQuantity.bind(this);
  }

  /** Esta fun√ß√£o "addState" √© respons√°vel por adicionar um item ao carrinho.
   * Basicamente, ele recebe por par√¢metro um produto espec√≠fico. Esse par√¢metro
   * √© um item que apareceu na home, assim que uma requisi√ß√£o √© feita, ou na
   * p√°gina de detalhes.
   * Isto √©, Details.jsx e CardItem.jsx tem o addState para esta tarefa. */
  addState(product) {
    /** Vari√°vel que ser√° editada, por isso em let it change üëÄ */
    let newCart = [];
    const { cart } = this.state;

    /** A const anyProduct ser√° criada de acordo com um item que foi
     * passado por par√¢metro na fun√ß√£o "addState". Ser√° passado um
     * .find() no cart, que vem do state, que deve ser um array de objetos.
     * AnyProduct ser√° a const respons√°vel por armazenar o primeiro
     * produto que satisfa√ßa a condi√ß√£o do .find(). Logo mais pra frente,
     * h√° uma verifica√ß√£o: Se o ID de item === ID do produto passado por
     * par√¢metro em "addState", traga esse produto para mim e jogue na vari√°vel
     * anyProduct. */
    const anyProduct = cart.find((item) => item.id === product.id);

    /** Caso a condi√ß√£o for atendida acima, anyProduct ficar√° true, e passar√° na
     * condicional abaixo */
    if (anyProduct) {
      /** newCart, pelo fato de ser let, pode ser modificada. Ent√£o, a cada itera-
       * √ß√£o do .map() abaixo ir√° veficiar se algum ID de "item" === ao ID do anyProduct.
       * Caso for igual, adicione +=1 na quantidade deste item e depois retorne-o.
       */
      newCart = cart.map((item) => {
        if (item.id === anyProduct.id) {
          item.quantity += 1;
          return item;
        }
        return item;
      });
    } else {
      /** Caso o produto ainda n√£o exista no carrinho, ser√° adicionado
       *  uma chave chamada "quantity" e o valor dela ser√° 1, significando
       * que √© o primeiro produto daquele tipo no carrinho */
      product.quantity = 1;
      /** Aqui abaixo √© feita a jun√ß√£o do carrinho com o produto, utilizando
       * spread, √© como se fosse um .push(), mas o mesmo n√£o deve ser utilizado
       * devido a problemas relacionados ao React, onde ele em vez dele unificar
       * o array, ele faz algo do tipo:
       * [Array anterior, [novo array]] */
      newCart = [...cart, product];
    }
    /** Ap√≥s o ocorrido, o newCart √© adicicionado ao cart */
    this.setState(() => ({ cart: newCart }));
  }

  /** Fun√ß√£o respons√°vel por incrementar a quantidade de itens ao carrinho. */
  increaseQuantity(product) {
    const { cart } = this.state;
    const newArr = cart.map((item) => {
      if (item.id === product.id) {
        item.quantity += 1;
        return item;
      }
      return item;
    });
    this.setState({
      cart: newArr,
    });
  }

  /** Fun√ß√£o respons√°vel por decrementar a quantidade de itens ao carrinho. */
  decreaseQuantity(product) {
    const { cart } = this.state;
    const newArr = cart.map((item) => {
      if (item.id === product.id && item.quantity > 0) {
        item.quantity -= 1;
        return item;
      }
      return item;
    });
    this.setState({
      cart: newArr,
    });
  }

  render() {
    const { cart } = this.state;
    return (
      <div>
        <BrowserRouter basename={ process.env.PUBLIC_URL }>
          <Switch>
            <Route exact path="/" render={ () => <Home addState={ this.addState } /> } />
            <Route
              exact
              path="/carrinho"
              render={ () => (<ShoppingCart
                cart={ cart }
                decreaseQuantity={ this.decreaseQuantity }
                increaseQuantity={ this.increaseQuantity }
              />) }
            />
            <Route
              exact
              path="/details/:id"
              render={ (props) => (<Details { ...props } addState={ this.addState } />) }
            />
          </Switch>
        </BrowserRouter>
      </div>
    );
  }
}

export default App;
